<template>
    <button id="reaction-test-btn" :style="{backgroundColor: backgroundColor}" @click="testReaction">
        <h1>{{textTitle}}</h1>
    </button>
</template>

<script>
export default {
    data() {
        return {
            readyToClick: false,
            backgroundColor: '#BA0021',
            textTitle: 'Wait for green',
            startTime: null,
            endTime: null,
        }
    },
    mounted() {
        const minDelay = 2000
        const maxDelay = 5000
        const reactionTestDelay = Math.floor(Math.random() * (maxDelay - minDelay + 1)) + minDelay
        setTimeout(() => {
            this.readyToClick = true
            this.backgroundColor = '#004225'
            this.textTitle = 'Click!'
            this.startTime = new Date().getTime();
        }, reactionTestDelay)
    },
    methods: {
        testReaction() {
            if (!this.readyToClick) {
                this.$emit('end', 'fail')
            } else {
                this.endTime = new Date().getTime();
                this.$emit('end', 'success', this.getReactionTime)
            }
        }
    },
    computed: {
        getReactionTime() {
            return this.endTime - this.startTime
        }
    }
}
</script>

<style>
#reaction-test-btn {
  width: 100%;
  height: 100%;
  border: none;
  cursor: pointer;
}

#reaction-test-btn h1 {
    color: white;
    font-size: 60px;
}
</style>

