<template>
    <div id="last-results-box">
        <h1>Last Results</h1>
        <ul>
            <li v-for="time in getLast5Results">{{ time }} ms</li>
        </ul>
    </div>
    <div id="average-box">
        <h1>Average</h1>
        <h2 v-if="getAverageTime>0">{{ getAverageTime.toFixed(1) }} ms</h2>
    </div>
</template>

<script>
export default {
    props: ['reactionsTime'],
    computed: {
        getLast5Results() {
            if (this.reactionsTime.length >= 5) {
                return this.reactionsTime.slice(-5)
            } else {
                return this.reactionsTime
            }
        },

        getAverageTime() {
            const sum = this.getLast5Results.reduce((acc, current) => acc + current, 0);
            if (this.reactionsTime.length >= 5) {
                return sum/5
            } else {
                return sum/this.reactionsTime.length
            }
        }
    }
}
</script>

<style>
    #last-results-box {
        position: absolute;
        top: 17.5%;
        left: 25%;
        width: 25%;
        height: 67.5%;
        background-color: white;
        border-radius: 5px;
        color: black;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 5px;
    }

    #last-results-box h1 {
        display: flex;
        justify-content: center;
        padding-top: 10px;
        font-size: 60px;
        margin-bottom: 0px;
    }

    #last-results-box ul {
        list-style: none;
        padding: 0;
    }

    #last-results-box ul li {
        display: flex;
        justify-content: center;
        font-size: 30px;
    }

    #average-box {
        position: absolute;
        top: 35%;
        left: 55%;
        width: 20%;
        height: 32.5%;
        background-color: white;
        border-radius: 5px;
        color: black;
        padding-top: 5px;
        box-shadow: rgba(0, 0, 0, 0.1) 0px 3px 6px;
    }

    #average-box h1, h2 {
        display: flex;
        justify-content: center; /* Horizontally center */
        align-items: center; /* Vertically center */
    }

    #average-box h1 {
        font-size: 60px;
    }

    #average-box h2 {
        font-size: 45px;
    }
</style>