<template>
  <div id="reaction-box">
    <WaitingReactionTest v-if="!isPlaying" @click="startTest" :waitingState="waitingState" :reactionTime="reactionsTime[reactionsTime.length - 1]" />
    <ReactionTest v-else @end="endTest" />
  </div>
  <div id="results-box">
    <TestResults :reactionsTime="reactionsTime" />
  </div>
</template>

<script>
import WaitingReactionTest from './components/WaitingReactionTest.vue'
import ReactionTest from './components/ReactionTest.vue'
import TestResults from './components/TestResults.vue'

export default {
  name: "app",
  components: {
    WaitingReactionTest,
    ReactionTest,
    TestResults
  },
  data () {
    return {
      isPlaying: false,
      waitingState: 'default',
      reactionsTime: [],
    }
  },
  methods: {
    startTest() {
      this.isPlaying = true
    },
    endTest(endState, reactionTime) {
      if (endState === 'fail') {
        this.waitingState = 'fail'
      } else {
        this.waitingState = 'success'
        this.reactionsTime.push(reactionTime)
      }
      this.isPlaying = false
    }
  },
}
</script>

<style>
#app {
  width: 100vw;
  height: 100vh;
}

#reaction-box {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 50%;
}

#results-box {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 50%;
  background-color: #f5f5f5;
}
</style>