<template>
  <button id="waiting-reaction-test-btn">
      <h1>{{ waitingText.title }}</h1>
      <p>{{ waitingText.paragraph }}</p>
  </button>
</template>

<script>
export default {
  props: ['waitingState', 'reactionTime'],
  data () {
    return {
      waitingText: {
        title: 'Reaction Time Test',
        paragraph: 'When the red box turns green, click as quickly as you can.\n Click anywhere to start.'
      }
    }
  },
  mounted() {
    if (this.waitingState === 'fail') {
      this.waitingText.title = 'Too Soon!'
      this.waitingText.paragraph = 'Click to try again.'
    } else if (this.waitingState === 'success') {
      this.waitingText.title = this.reactionTime + ' ms'
      this.waitingText.paragraph = 'Click to keep going'
    }
  }
}
</script>

<style>
#waiting-reaction-test-btn {
  width: 100%;
  height: 100%;
  background-color: #219ebc;
  border: none;
  cursor: pointer;
}

#waiting-reaction-test-btn h1 {
  color: white;
  font-size: 60px;
}

#waiting-reaction-test-btn p {
  color: white;
  white-space: pre-line;
  font-size: 30px;
}
</style>